<script>
	import imgList from '@/components/imglist.vue'
	export default {
		data () {
			return {
				defaultList: [],
				imgName: '',
				visible: false,
				loanInfo: {},
				baseInfo: {},
				logList:[]
			}
		},
		components:{
			imgList
		},
		created: function () {
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			this.loadInfo();
		},
		methods: {
			handleView (name) {
				this.imgName = name;
				this.visible = true;
			},
			loadInfo: function () {
				let _this = this;
				let params = _this.$route.params;
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('loanManage/applicantDetails', {
					loanId: params.loanId,
					serialNo:params.serialNo
				})
					.then(function (response) {
						let res = response.data;
						if (res.code == '1') {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.loanInfo = res.data;
							_this.defaultList=JSON.parse(res.data.grantMoney.loanImage);
							_this.$nextTick(function () {
								let index=parseFloat(res.data.logViewList.length)-1;
								_this.logList=['panel'+index];
							})
						} else {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.$Notice.error({
								title: res.msg,
								duration: 3
							});
							_this.$router.go(-1);
						}
					})
					.catch(function (error) {
						//处理错误
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Notice.error({
							title: error,
							duration: 3
						});
					});
			},
		}
	}
</script>
